.pet-container {
  .landing-form-img-container {
    margin-top: -80px;

  
    @media screen and (max-width: 576px) {
      margin-top: -30px;
    }
    .img-3 {
      height: 50%;
    }
  }

  .promo-outer-extra {
    margin-top: -40px;

    @media screen and (max-width: 576px) {
      margin-top: -60px !important;
      .desc {
        max-width: 100%;
      }
    }

    @media screen and (max-width: 400px) {
      margin-top: -80px !important;
      .desc {
        max-width: 100%;
        margin-top: -180px !important;
      }
    }
  }

  .promo-img-extra {
    @media screen and (max-width: 576px) {
      margin-top: -50px;
      .desc {
        max-width: 100%;
      }
    }

    @media screen and (max-width: 400px) {
      margin-top: 50px;
      .desc {
        max-width: 100%;
      }
    }
  }
}