.header-desc-with-button-container {
  min-height: 125px;
  border-bottom: solid 1px #C8E0E4; }
  .header-desc-with-button-container .insurer-logo-container {
    max-width: 120px;
    display: flex;
    justify-content: center;
    align-items: center; }
    .header-desc-with-button-container .insurer-logo-container .company-logo-container {
      display: flex;
      justify-content: center;
      align-items: center; }
      .header-desc-with-button-container .insurer-logo-container .company-logo-container .company-logo {
        max-width: 70px;
        max-height: 70px; }
    @media screen and (max-width: 690px) {
      .header-desc-with-button-container .insurer-logo-container {
        max-width: 100px; } }
  .header-desc-with-button-container .insurer-desc-container {
    padding: 22px;
    padding-bottom: 20px; }
    .header-desc-with-button-container .insurer-desc-container .insurer-desc-top .insurer-name {
      font-size: 20px;
      font-weight: bold;
      color: #354F53;
      margin: 0; }
    .header-desc-with-button-container .insurer-desc-container .desc {
      font-size: 16px;
      font-weight: 200;
      color: #46737A; }
    @media screen and (max-width: 690px) {
      .header-desc-with-button-container .insurer-desc-container {
        padding: 15px; }
        .header-desc-with-button-container .insurer-desc-container .insurer-desc-top .insurer-name {
          font-size: 14px;
          font-weight: 800; }
        .header-desc-with-button-container .insurer-desc-container .desc {
          font-size: 10px;
          font-weight: normal;
          margin-top: 5px; } }
  .header-desc-with-button-container .header-button-container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 28px; }
    @media screen and (max-width: 690px) {
      .header-desc-with-button-container .header-button-container {
        flex: 100%;
        margin-right: 0;
        margin-bottom: 20px; }
        .header-desc-with-button-container .header-button-container .button-medium-color {
          min-width: 200px !important;
          height: 42px !important; }
          .header-desc-with-button-container .header-button-container .button-medium-color p {
            font-size: 16px !important; } }
